import { Button, Heading, Text, useLocalStorage, HStack, VStack, Input, Flex, Dialog, useDisclosure, useNotice } from "@yamada-ui/react";
import { useEffect, useState } from "react";

export function Settings() {
  const [dataURL, setDataURL, resetDataURL] = useLocalStorage<string>({
    key: "dataURL",
  });
  const [dataURLInput, setDataURLInput] = useState<string>("");
  const notice = useNotice();
  const { isOpen, onOpen, onClose } = useDisclosure({
    
  });

  useEffect(() => {
    setDataURLInput(dataURL??"");
  }, [dataURL])

  const onSaveDataURL = () => {
    setDataURL(dataURLInput);
    notice({
      title: "URL",
      description: "",
      status: "success",
      placement: "top-right",
    });
  };

  const resetAllLocalStrage = () => {
    resetDataURL();
  }

  return (
    <>
      <Flex direction="column" gap="md">
        <VStack>
          <Heading>URL</Heading>
          <HStack>
            <Input placeholder="URL" value={dataURLInput} onChange={(e) => setDataURLInput(e.target.value)} />
            <Button onClick={onSaveDataURL} colorScheme="primary"></Button>
          </HStack>
        </VStack>
        <VStack>
          <Button onClick={onOpen} colorScheme="danger"></Button>
          <Dialog
            isOpen={isOpen}
            header=""
            cancel=""
            success={{ colorScheme: "danger", children: "" }}
            onClose={onClose}
            onSuccess={() => {
              resetAllLocalStrage();
              onClose();
            }}
          >
            <Text></Text>
          </Dialog>
        </VStack>
      </Flex>
    </>
  );
}
